.main-footer {
  position: relative;

  margin: 0 auto;

  @media (min-width: $vp-1150) {
    max-width: 1150px;
  }
}

.main-footer__container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100vw;
    z-index: -1;

    width: 200vw;
    height: 100%;

    background-color: $color-white-smoke;
  }

  @media (min-width: $vp-768) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 51px 50px 58px;
  }
}

.main-footer__copyright {
  font-size: 19px;
  line-height: 18px;
  color: $color-black;
}

.main-footer__icon {
  &--academy {
    margin-top: -5px;

    fill: $color-downy;
  }

  &:hover,
  &:focus {
    fill: $color-puerto;
  }

  &:active {
    fill: rgba($color-puerto, 0.3);
  }
}

.main-footer__link:focus .main-footer__icon,
.main-footer__link:hover .main-footer__icon {
  fill: $color-puerto;
}

.main-footer__link:active .main-footer__icon {
  fill: rgba($color-puerto, 0.3);
}

.main-footer__logo {
  display: none;

  @media (min-width: $vp-768) {
    display: block;
  }
}

.main-footer__link {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 29px 39px 32px;

  text-decoration: none;

  border-top: 1px solid $color-grey;

  gap: 27px;

  @media (min-width: $vp-768) {
    width: 182px;
    margin: 0;
    padding: 0;

    border-top: none;

    gap: 0;
  }
}
